<script>
  import * as L from "leaflet";


  export default {
    filters: {
      ellipsis_1(value) {
        if (!value) return '';
        let num = parseFloat(value);
        return num.toFixed(2);
      },
    },
    data() {
      return {
        fieldBar: [],
        temSalBar:[],
        value1: '',
        radio: '2',
        ifTable: false,
        ifDeep: false,
        ifField: false,
        ifQuery: false,
        ifAnimation: false,
        ifEddyLife: false,
        ifTem: false,
        ifSal: false,
        animationbg: false,
        customBaselayer: '',
        backgroundDiv: {
          backgroundImage: 'url(' + require('@/assets/img/headbackground.png') + ')',
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'auto 100%',
          backgroundPosition: 'center center'
        },
        deepOptions: [{value: '0', label: '深度：0m'}, {value: '50', label: '深度：50m'}, {value: '100', label: '深度：100m'},
          {value: '150', label: '深度：150m'}, {value: '200', label: '深度：200m'}, {value: '250', label: '深度：250m'},
          {value: '300', label: '深度：300m'}, {value: '350', label: '深度：350m'}, {value: '400', label: '深度：400m'},],
        deep: '0',
        options: [{value: '0', label: '卫星高度计'}, {value: '1', label: 'Hycom'}],
        data_type: '0',
        map_options: [{value: '基础', label: '基础'}, {value: '灰色', label: '灰色'},
          {value: '青蓝', label: '青蓝'}, {value: '紫蓝', label: '紫蓝'}, {value: '暖色', label: '暖色'},],
        map_type: '基础',
        data_type_show: '',
        date: '',
        date_show: '请选择日期',
        W_show: '0',
        E_show: '0',
        S_show: '0',
        N_show: '0',
        number_show: '0',
        polygon: [],
        polygon_center: '请选择左侧涡旋',  //漩涡中心点
        polygon_radius: '请选择左侧涡旋',  //漩涡半径
        polygon_white: '',
        img_date: require('@/assets/img/date.png'),
        img_area: require('@/assets/img/area.png'),
        img_data: require('@/assets/img/data.png'),
        img_number: require('@/assets/img/number.png'),
        map: "",
        point: [22.0, 118.0],
        eddy_data: {
          "status": 0,
          "data": {
            "dateString": "2020-11-23",
            "areaCoordinate": {
              "areaCenterLat": "22.0",
              "areaCenterLon": "118.0",
              "latMin": "2",
              "latMax": "42",
              "lonMin": "104",
              "lonMax": "132"
            },
            "lstEddies": [
              {
                "name": null,
                "longitude": "124.0629",
                "latitude": "9.382177",
                "direction": 1,
                "radius": "30551.264",
                "shapeLons": [],
                "shapeLats": []
              }
            ]
          }
        },
        data: [],
        mousePosition: '',
        velocityLayer: '',
        time1: '',
        eddyLifeMap: '',
        eddyId: '',
        TemSal: '',
        eddyLifeId:'',
        eddyLifePolygon:[],
        eddyLifeData:{
          "status": 0,
          "data": {
            "lstOneLifeEddy": [
              {
                "id": null,
                "name": null,
                "longitude": "113.625",
                "latitude": "17.625",
                "direction": 1,
                "radius": "43.9523",
                "shapeLons": [],
                "shapeLats": [],
                "lifecycleId": 206,
                "lifeStartTime": "20201205",
                "dateString": "20201205",
                "dataType": 0
              },
            ]
          },
          "statusDesc": "成功"
        },
        eddyLifePolygonY:'',
      };
    },
    created() {

    },
    mounted() {
      this.initDate();  //初始化leaflet只能在挂载函数里
      this.getTodayData();

    },
    methods: {
      eddyLifeClose(){
        this.ifEddyLife=false;
      },
      changeFieldBar(val) {
        let that = this;
        if (val) {
          if (val.indexOf('流场') > -1) {
            that.drawVelocity();
          } else if (that.velocityLayer) {
            that.map.removeLayer(that.velocityLayer);
          }



        }
      },

      changeTemSalBar(val){
        let that = this;
        if (val.indexOf('温度') > -1) {
          that.drawTemSal('water_temp');
          that.ifSal = true;
        } else if ((val.indexOf('盐度') > -1) && that.TemSal) {
          that.ifTem = true;
          that.map.removeLayer(that.TemSal);
          that.drawTemSal('salinity');
        } else if (that.TemSal) {
          that.map.removeLayer(that.TemSal);
          that.ifTem = false;
          that.ifSal = false;
        }
      },

      showTable() {
        this.ifTable = !this.ifTable;
      },
      showDeep(val) {
        this.ifDeep = val == 1;
      },
      selectDate() {
        this.animationbg = true;
        this.axios.post('http://192.168.83.122:8085/eddy/image', {
          "endTime": this.value1[1],
          "startTime": this.value1[0]
        })
          .then(function (response) {
            let imgList = response.data.data.url;
            var head = document.getElementById("animation");//获取DIV对象
            // head.style.background="url(http://192.168.83.122:8085/"+imgList[0]+")";   //设置图片的初始图片为该路径的图片
            // head.style.backgroundSize="cover";
            let bg = document.getElementsByClassName('animationbg')[0];
            // bg.style.background='#fff';
            head.style.background = "url(http://192.168.83.122:8085/" + imgList[0] + ")";
            head.style.backgroundSize = "cover";
          })
          .catch(function (error) {
            console.log(error);
          });
      },

      // closeAnimation(){
      //   this.ifAnimation=false;
      //   document.getElementsByClassName('options')[1].style.width='70px'
      // },
      clickMenu(n) {
        let bFather = document.getElementById('bFather');
        let options = bFather.getElementsByTagName('button');
        for (let i = 0; i < options.length; i++) {
          options[i].style.width = '70px';
        }
        options[n].style.width = '100px';
        if (n === 0) {
          this.ifQuery = true;
          this.ifAnimation = false;
          this.ifField = false;
          this.animationbg = false;
        } else if (n === 1) {
          this.ifQuery = false;
          this.ifAnimation = true;
          this.ifField = false;

        } else if (n === 2) {
          this.ifQuery = false;
          this.ifAnimation = false;
          this.ifField = true;
          this.animationbg = false;
        } else {
          this.ifQuery = false;
          this.ifAnimation = false;
          this.ifField = false;
        }
      },

      initDate() {
        this.map = L.map('map').setView(this.point, 6);
        this.customBaselayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}.png', {
          attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        });
        this.map.addLayer(this.customBaselayer);

        L.latlngGraticule({
          showLabel: true,
          dashArray: [0, 0],
          zoomInterval: [
            {start: 2, end: 3, interval: 30},
            {start: 4, end: 4, interval: 10},
            {start: 5, end: 7, interval: 10},
            {start: 8, end: 10, interval: 1}
          ]
        }).addTo(this.map);

        this.mousePosition = L.control.mousePosition().addTo(this.map);

        // let that=this
        // d3.text("http://192.168.83.122:39119/upload/20201204/8/8/temp_0.asc", function (asc) {
        //   var s = L.ScalarField.fromASCIIGrid(asc);
        //   var layer = L.canvasLayer.scalarField(s).addTo(that.map);
        //
        //   that.map.fitBounds(layer.getBounds());
        //
        //   // layer.on('click', function (e) {
        //   //   if (e.value !== null) {
        //   //     let v = e.value.toFixed(3);
        //   //     let html = `<span class="popupText">Surface currents velocity ${v} m/s</span>`;
        //   //     let popup = L.popup().setLatLng(e.latlng).setContent(html).openOn(that.map);
        //   //   }
        //   // });
        // });


        var normalm1 = L.tileLayer.chinaProvider('Geoq.Normal.Map', {maxZoom: 18, minZoom: 1});
        var normalm3 = L.tileLayer.chinaProvider('Geoq.Normal.PurplishBlue', {
          maxZoom: 18,
          minZoom: 1
        });
        var normalm4 = L.tileLayer.chinaProvider('Geoq.Normal.Gray', {
          maxZoom: 18,
          minZoom: 1
        });
        var normalm5 = L.tileLayer.chinaProvider('Geoq.Normal.Warm', {
          maxZoom: 18,
          minZoom: 1
        });

        /**
         * 天地图内容
         */
        var normalm = L.tileLayer.chinaProvider('TianDiTu.Normal.Map', {
            key: '3bd04d49edc092df370712b3fada8d7b',
            maxZoom: 18,
            minZoom: 1,
          }),
          normala = L.tileLayer.chinaProvider('TianDiTu.Normal.Annotion', {
            maxZoom: 18,
            minZoom: 1
          }),
          imgm = L.tileLayer.chinaProvider('TianDiTu.Satellite.Map', {
            maxZoom: 18,
            minZoom: 1
          }),
          imga = L.tileLayer.chinaProvider('TianDiTu.Satellite.Annotion', {
            maxZoom: 18,
            minZoom: 1
          });

        var normal = L.layerGroup([normalm, normala]);

        /**
         * 高德地图
         */
        var Gaode = L.tileLayer.chinaProvider('GaoDe.Normal.Map', {
          maxZoom: 18,
          minZoom: 1
        });
        var Gaodimgem = L.tileLayer.chinaProvider('GaoDe.Satellite.Map', {
          maxZoom: 18,
          minZoom: 1
        });
        var Gaodimga = L.tileLayer.chinaProvider('GaoDe.Satellite.Annotion', {
          maxZoom: 18,
          minZoom: 1
        });
        var Gaodimage = L.layerGroup([Gaodimgem, Gaodimga]);


        var baseLayers = {
          "智图地图": normalm1,
          "智图午夜蓝": normalm3,
          "智图灰色": normalm4,
          "智图暖色": normalm5,
          "天地图": normal,
          "高德地图": Gaode,
          "高德影像": Gaodimage,
        }

        L.control.layers(baseLayers, null).addTo(this.map);
      },

      changeMap(value) {
        let that = this;
        that.map.removeLayer(that.customBaselayer);
        switch (value) {
          case '基础':
            that.customBaselayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}.png', {
              attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            });
            break;
          case '灰色':
            that.customBaselayer = L.tileLayer('http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetGray/MapServer/tile/{z}/{y}/{x}', {
              attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            });
            break;
          case '青蓝':
            that.customBaselayer = L.tileLayer('http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineCommunity/MapServer/tile/{z}/{y}/{x}', {
              attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            });
            break;
          case '紫蓝':
            that.customBaselayer = L.tileLayer('http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer/tile/{z}/{y}/{x}', {
              attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            });
            break;
          case '暖色':
            that.customBaselayer = L.tileLayer('http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetWarm/MapServer/tile/{z}/{y}/{x}', {
              attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            });
            break;
        }
        that.map.addLayer(that.customBaselayer);
      },

      cancel() {
        let that = this;
        this.map.off('mousedown');
        this.map.off('mouseup');
        this.map.dragging.enable();
        if (typeof that.rectangle != 'undefined') {
          that.rectangle.remove();
        }

      },

      a() {  //画框函数
        let that = this;
        if (typeof that.rectangle != 'undefined') {
          that.rectangle.remove();
        }
        if (that.polygon_yellow) {  //移除黄色漩涡
          that.map.removeLayer(that.polygon_yellow);
        }
        const latlngs = [];
        that.map.on('mousedown', onClick);    //点击地图
        that.map.on('mouseup', onDoubleClick);

        for (let k = 0; k < that.polygon.length; k++) {  ////移除所有漩涡
          that.map.removeLayer(this.polygon[k]);
        }

        function onClick(e) {
          // that.map.removeLayer(that.rectangle1)

          //左上角坐标
          latlngs[0] = [e.latlng.lat, e.latlng.lng];
          //开始绘制，监听鼠标移动事件
          that.map.on('mousemove', onMove);

        }

        function onMove(e) {
          //取消地图的拖拽方式
          that.map.dragging.disable();
          //
          latlngs[1] = [e.latlng.lat, e.latlng.lng];
          //删除临时矩形
          if (typeof that.tmprect != 'undefined') {
            that.tmprect.remove();
          }
          //添加临时矩形
          that.tmprect = L.rectangle(latlngs, {dashArray: 5}).addTo(that.map);
          // console.log(that.tmprect, 'tmprect1')
        }

        function onDoubleClick(e) {
          //矩形绘制完成，移除临时矩形，并停止监听鼠标移动事件
          // console.log(that.tmprect, 'tmprect2')
          that.tmprect.remove();
          that.map.off('mousemove');
          //右下角坐标
          latlngs[1] = [e.latlng.lat, e.latlng.lng];
          that.rectangle = L.rectangle(latlngs, {
            color: '#6c54e8',
            fillOpacity: .2,
            weight: 3
          })
          that.rectangle.addTo(that.map);

          that.map.off('mousedown');
          that.map.off('mouseup');
          that.map.dragging.enable();

          if (latlngs[0][1] > latlngs[1][1]) {
            that.lonMax = latlngs[0][1];
            that.lonMin = latlngs[1][1];
          } else {
            that.lonMax = latlngs[1][1];
            that.lonMin = latlngs[0][1];
          }
          if (latlngs[0][0] > latlngs[1][0]) {
            that.latMax = latlngs[0][0];
            that.latMin = latlngs[1][0];
          } else {
            that.latMax = latlngs[1][0];
            that.latMin = latlngs[0][0];
          }

          that.getData();

          // if (that.polygon[0]) {
          //   for (let i = 0; i < that.polygon.length; i++) {
          //     if ((that.polygon[i].longitude < latlngs[0][1] && that.polygon[i].longitude < latlngs[1][1]) || (that.polygon[i].longitude > latlngs[0][1] && that.polygon[i].longitude > latlngs[1][1]) || (that.polygon[i].latitude < latlngs[0][0] && that.polygon[i].latitude < latlngs[1][0]) || (that.polygon[i].latitude > latlngs[0][0] && that.polygon[i].latitude > latlngs[1][0])) {
          //       that.map.removeLayer(that.polygon[i]);
          //     }
          //   }
          // }
        }
      },

      getTodayData() {
        let that = this;
        // this.axios.post('http://192.168.1.102:8072/eddy/records?dateString='+this.today)
        this.axios.post('http://192.168.1.101:9002/eddy/records')
          .then(function (response) {
            that.eddy_data = response.data;
            that.drawPolygon();

            that.date = that.eddy_data.data.dateString.slice(0, 4) + '-' + that.eddy_data.data.dateString.slice(4, 6) + '-' + that.eddy_data.data.dateString.slice(6, 8);
          })
          .catch(function (error) {
            console.log(error);
          });
      },

      getData() {
        for (let k = 0; k < this.polygon.length; k++) {
          this.map.removeLayer(this.polygon[k]);
        }

        let that = this;
        this.axios.post('http://192.168.1.101:9002/eddy/records?dataType=' + that.data_type + '&dateString=' + that.date + '&depth=' + that.deep)
          .then(function (response) {
            that.eddy_data = response.data;
            if (that.eddy_data.data) {
              that.drawPolygon();

            }


          })
          .catch(function (error) {
            console.log(error);
          });
        if (that.fieldBar.indexOf('流场') > -1) {
          that.drawVelocity();
        }
        if (that.temSalBar.indexOf('温度') > -1) {
          that.drawTemSal('water_temp');
        }
        if (that.temSalBar.indexOf('盐度') > -1) {
          that.drawTemSal('salinity');
        }
      },

      getAnimationImg() {
        this.animationbg = true;
        let that = this;
        let imgList = [];
        this.axios.post('http://192.168.83.122:8085/eddy/image', {
          "endTime": this.value1[1],
          "startTime": this.value1[0]
        })
          .then(function (res) {
            //成功
            imgList = res.data.data.url;
            var head = document.getElementById("animation");//获取DIV对象
            // head.style.background="url(http://192.168.83.122:8085/"+imgList[0]+")";   //设置图片的初始图片为该路径的图片
            // head.style.backgroundSize="cover";
            let bg = document.getElementsByClassName('animationbg')[0];
            // bg.style.background='#fff';

            window.clearTimeout(that.time1)

            for (var i = 0; i < imgList.length; i++) {
              (function (i) {
                that.time1 = setTimeout(function () {
                  head.style.background = "url(http://192.168.83.122:8085/" + imgList[i] + ")";
                  head.style.backgroundSize = "cover";
                }, i * 1000);
              })(i);
            }


          }.bind(this))
          .catch(function (err) {
            if (err.response) {
              //失败
              console.log(err.response)
            }
          }.bind(this))
        // var imgs =["head0.jpg", "head1.jpg", "head2.jpg"];    //（设定想要显示的图片）


      },

      drawPolygon() {
        for (let j = 0; j < this.eddy_data.data.lstEddies.length; j++) {
          let latlngs = [];
          let polygon;

          for (let i = 0; i < this.eddy_data.data.lstEddies[j].shapeLons.length; i++) {
            let b = [this.eddy_data.data.lstEddies[j].shapeLats[i], this.eddy_data.data.lstEddies[j].shapeLons[i]];
            latlngs.push(b);
          }
          if (this.eddy_data.data.lstEddies[j].direction === 1) {
            this.polygon[j] = L.polygon(latlngs, {color: 'red'}).addTo(this.map);  //创建蓝色多边形
          } else {
            this.polygon[j] = L.polygon(latlngs, {color: 'blue'}).addTo(this.map);  //创建红色多边形
          }
          this.polygon[j].id = this.eddy_data.data.lstEddies[j].id;
          this.polygon[j].longitude = this.eddy_data.data.lstEddies[j].longitude;
          this.polygon[j].latitude = this.eddy_data.data.lstEddies[j].latitude;
          this.polygon[j].radius = this.eddy_data.data.lstEddies[j].radius;


          let LeafIcon = L.Icon.extend({
            options: {
              shadowUrl: '',
              iconSize: [0, 0],
              // shadowSize:   [50, 64],
              iconAnchor: [0, 0],
              // shadowAnchor: [4, 62],
              popupAnchor: [0, -10]
            }
          });
          let greenIcon = new LeafIcon({iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png'});


          let that = this;
          this.polygon[j].on('click', function () {

            L.marker([this.latitude, this.longitude], {icon: greenIcon}).addTo(that.map)
              .bindPopup('涡旋id：' + this.id + '<br>纬度：' + this.latitude + '°<br>经度：' + this.longitude + '°<br>半径：' + this.radius + 'km' + '<br>生命周期：<button class="eddyLife">查看</button>')
              .openPopup();
            that.eddyId = this.eddyId;

            let eddyLife = document.getElementsByClassName('eddyLife');
            that.eddyLifeId=this.id;
            for (let i = 0; i < eddyLife.length; i++) {
              eddyLife[i].addEventListener("click", that.eddyLifeShow);
            }
          });
        }
      },

      drawVelocity() {
        let that = this;
        if (this.velocityLayer) {
          this.map.removeLayer(this.velocityLayer);
        }

        // this.axios.get("/api/majorlist")
        this.axios.post("http://192.168.1.101:9002/eddy/current?dataType=" + that.data_type + "&dateString=" + that.date + "&depth=" + that.deep)
          .then(function (response) {
            let data = response.data.data;
            //生成风场实例
            if (data) {
              that.velocityLayer = L.velocityLayer({
                displayValues: true,
                displayOptions: {
                  velocityType: 'GBR flow',
                  displayPosition: 'bottomleft',
                  displayEmptyString: 'No flow data'
                },
                data: response.data.data,//风场数据
                minVelocity: -5, //Velocity：速率
                maxVelocity: 5,
                velocityScale: 1 / 2,

                particleAge: 100,  // 粒子在再生之前绘制的最大帧数
                particleMultiplier: 1 / 200,//粒子的数量
                lineWidth: 1,                     //粒子的粗细
                frameRate: 7,                      //定义每秒执行的次数,流速
                colorScale: ['#ffffff']
              });

              //添加风场样式至地图中

              // 风场实例添加到地图上
              that.velocityLayer.addTo(that.map)
            }
          })
          .catch(function (error) {
            console.log("出错喽：" + error);
          });
      },

      drawTemSal(val) {
        let that = this;
        if (this.TemSal) {
          this.map.removeLayer(this.TemSal);
        }
if(this.data_type==1){
        this.axios.post("http://192.168.1.101:9002/eddy/tempsalt", {
          "dataType": that.data_type,
          "dateString": that.date,
          "depth": that.deep
        })
          .then(function (response) {
            let data = response.data.data;
            //生成风场实例
            if (data) {
              that.TemSal = L.tileLayer.wms(data.baseUrl, {
                // REQUEST:'GetLegendGraphic',
                // PALETTE:'11',
                ELEVATION:-that.deep,
                layers: val,
                format: 'image/png',
                transparent: true,
                // srs: 'EPSG:4326',
                // attribution: "Weather data © 2012 IEM Nexrad"
              }).addTo(that.map);

            }
          })
          .catch(function (error) {
            console.log("出错喽：" + error);
          });
}
      },

      // changeField(val) {
      //   if (this.velocityLayer) {
      //     this.map.removeLayer(this.velocityLayer);
      //   }
      //   if (val == 1) {
      //     this.drawVelocity();
      //   }
      // },

      initEddyLife() {
        this.eddyLifeMap=''
        if (!this.eddyLifeMap) {
          this.eddyLifeMap = L.map('eddyLifeMap').setView(this.point, 6);
          this.customBaselayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}.png', {
            attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          });
          this.eddyLifeMap.addLayer(this.customBaselayer);
        }
      },

      eddyLifeShow() {
        let that = this;
        that.ifEddyLife = false;
        setTimeout(()=>{that.ifEddyLife = true;},0)



        this.axios.post('http://192.168.1.101:9002/eddy/eddylife', {"eddyCenterId": that.eddyLifeId})
          .then(function (response) {
            that.initEddyLife();

            let point = [response.data.data.lstOneLifeEddy[0].latitude, response.data.data.lstOneLifeEddy[0].longitude];
            that.eddyLifeMap.setView(point, 6);

            that.eddyLifeData = response.data;

            for (let j = 0; j < that.eddyLifeData.data.lstOneLifeEddy.length; j++) {
              let latlngs = [];
              let polygon;

              for (let i = 0; i < that.eddyLifeData.data.lstOneLifeEddy[j].shapeLons.length; i++) {
                let b = [that.eddyLifeData.data.lstOneLifeEddy[j].shapeLats[i], that.eddyLifeData.data.lstOneLifeEddy[j].shapeLons[i]];
                latlngs.push(b);
              }
              if (that.eddyLifeData.data.lstOneLifeEddy[j].direction === 1) {
                that.eddyLifePolygon[j] = L.polygon(latlngs, {color: 'red'}).addTo(that.eddyLifeMap);  //创建蓝色多边形
              } else {
                that.eddyLifePolygon[j] = L.polygon(latlngs, {color: 'blue'}).addTo(that.eddyLifeMap);  //创建红色多边形
              }
            }


          })
          .catch(function (error) {
            console.log(error);
          });
      },
      drawPolygonById(id){
if(this.eddyLifePolygonY){
  this.eddyLifeMap.removeLayer(this.eddyLifePolygonY);
}


        let that=this;
        for(let j=0;j<that.eddyLifeData.data.lstOneLifeEddy.length;j++){
          if(that.eddyLifeData.data.lstOneLifeEddy[j].id==id){
            let latlngs = [];
            let polygon;

            for (let i = 0; i < that.eddyLifeData.data.lstOneLifeEddy[j].shapeLons.length; i++) {
              let b = [that.eddyLifeData.data.lstOneLifeEddy[j].shapeLats[i], that.eddyLifeData.data.lstOneLifeEddy[j].shapeLons[i]];
              latlngs.push(b);
            }
            that.eddyLifePolygonY = L.polygon(latlngs, {color: '#fdf902'}).addTo(that.eddyLifeMap);  //创建蓝色多边形
          }
        }
      }
    }
  };
</script>

<style scoped>
  #map {
    width: 100%;
    height: calc(100vh);
    z-index: 1;
  }

  /deep/ .el-input__inner {
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: 0;
  }

  /deep/ .el-picker-panel {
    background: rgba(0, 0, 0, 0.5);
  }

  /deep/ .leaflet-container a.leaflet-popup-close-button {
    display: none;
  }

  /deep/ .el-range-input {
    background: rgba(0, 0, 0, 0);
    color: white;
  }

  /deep/ .el-radio {
    color: white;
  }

  /deep/ .el-date-editor .el-range-separator {
    color: white;
  }

  .options {
    position: absolute;
    width: 70px;
    top: 110px;
    left: 60px;
    overflow: hidden;
    z-index: 11;
    transition: width 0.3s;
    -webkit-transition: width 0.3s; /* Safari */
  }

  .options:hover {
    width: 100px;
  }

  .animationbg {
    background-color: #fff;
  }

  .tableItem:hover {
    background-color: rgba(255, 255, 255, 0.5);
    color: white;
  }
</style>



