<template>
  <div style="position: relative;font-size: 10px">
    <div id="map" style="position: absolute;top: 0"></div>
    <div class="head">
      <div class="welcome">欢迎来到中尺度涡旋自动探测软件集成优化与可视化平台！</div>
      <div class="menu" :style="backgroundDiv">
        <router-link :to="{path:'/'}">
          <div>涡旋可视化</div>
        </router-link>
        <router-link :to="{path:'/fuwu'}">
          <div>数据服务</div>
        </router-link>
        <div class="this">涡旋统计</div>
      </div>
    </div>

    <div style="width: 1400px;position:relative;z-index: 10;padding-top: 110px;margin: 0 auto;font-size: 10px">
      <div style="width: 136em;height: 70em;padding: 2em;">
        <div style="">
          <el-date-picker
            v-model="minStartTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="开始日期">
          </el-date-picker>
          <el-date-picker
            v-model="maxStartTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="结束日期">
          </el-date-picker>
          <el-select v-model="eddyType" placeholder="请选择">
            <el-option
              v-for="item in eddyTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-select v-model="dataType" placeholder="请选择">
            <el-option
              v-for="item in dataTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-button type="info" @click="getTableData()">查询</el-button>
          <el-button style="float: right" type="info" @click="exportData()">数据导出</el-button>
        </div>
        <!--        <div style="display: flex;flex-wrap: wrap;margin-top: 30px;color: white;font-size: 1.6em">-->
        <!--          <div style="width: 100px;text-align: center;">名称</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋出现时间</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋id</div>-->
        <!--          <div style="width: 200px;text-align: center;">探测日期</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋中心点经度</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋中心点纬度</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋类型</div>-->
        <!--          <div style="width: 100px;text-align: center;">涡旋半径</div>-->
        <!--        </div>-->
        <el-table
          :data="tableData"
          border
          style="width: 100%;margin-top: 20px;background-color: red!important;">
          <el-table-column
            prop="countNum"
            label="涡旋总数"
            >
          </el-table-column>
          <el-table-column
            prop="cyclonicNum"
            label="气旋数"
           >
          </el-table-column>
          <el-table-column
            prop="anticyclonicNum"
            label="反气旋数">
          </el-table-column>
          <el-table-column
            prop="duration"
            label="最长存活时间">
          </el-table-column>
        </el-table>

          <el-table
            :data="listData.data.lstEddy"
            :span-method="objectSpanMethod"
            border
            height="470px"
            class="tableArea"
            style="width: 100%;margin-bottom: 20px">
            <el-table-column
              prop="id"
              label="ID"
              align="center"
             />
            <el-table-column
              prop="startTime"
              label="涡旋出现时间"
              align="center"/>
            <el-table-column
              prop="eddyId"
              label="涡旋ID"
              align="center"/>
            <el-table-column
              prop="trackDate"
              label="探测日期"
              align="center"/>
            <el-table-column
              prop="dataType"
              label="数据来源"
              align="center">
              <template slot-scope="scope">{{ scope.row.dataType | dataTypeEllipsis }}</template>
            </el-table-column>
            <el-table-column
              prop="longitude"
              label="涡旋中心点经度"
              align="center">
              <template slot-scope="scope">{{ scope.row.longitude | longitudeEllipsis }}</template>
            </el-table-column>
            <el-table-column
              prop="latitude"
              label="涡旋中心点纬度"
              align="center">
              <template slot-scope="scope">{{ scope.row.latitude | longitudeEllipsis }}</template>
            </el-table-column>
            <el-table-column
              prop="direction"
              label="涡旋类型"
              align="center">
              <template slot-scope="scope">{{ scope.row.direction | directionEllipsis }}</template>
            </el-table-column>
            <el-table-column
              prop="radius"
              label="半径"
              align="center"/>
          </el-table>


      </div>
    </div>
  </div>
</template>

<script>
  import * as L from "leaflet";


  export default {
    filters: {
      dataTypeEllipsis(value) {
        return value==0?'高度计':'Hycom';
      },
      longitudeEllipsis(value) {
        return value+'°';
      },
      directionEllipsis(value) {
        return value==0?'气旋':'反气旋';
      },
    },
    data() {
      return {
        eddyType: 'null',
        eddyTypeOptions: [{value: 'null', label: '全部气旋'}, {value: '0', label: '气旋'}, {value: '1', label: '反气旋'},],
        dataType: '0',
        dataTypeOptions: [{value: '0', label: '高度计'}, {value: '1', label: 'Hycom'}],
        rowList: [],
        spanArr: [],
        position: 0,
        tableData: [{"countNum": 0,
          "cyclonicNum": 0,
          "duration": 0,
          "anticyclonicNum":0}],
        listData: {
          "status": 0,
          "data": {

          }
        },

        backgroundDiv: {
          backgroundImage: 'url(' + require('@/assets/img/headbackground.png') + ')',
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'auto 100%',
          backgroundPosition: 'center center'
        },
        point: [17.883392, 130.90785],
        minStartTime:'2020-12-01',
        maxStartTime: '',
      };
    },
    created() {

    }
    ,
    mounted() {
      this.initDate();  //初始化leaflet只能在挂载函数里
      this.getToday();
    }
    ,
    methods: {
      getToday(){
        let today=new Date();
        let month=today.getMonth()+1;
        let date=today.getDate()/10>=1?today.getDate():'0'+today.getDate();
        this.maxStartTime=today.getFullYear()+'-'+month+'-'+date;
        this.getTableData()
      },
      initDate() {
        this.map = L.map('map').setView(this.point, 6);
        this.customBaselayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}.png', {
          attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        });
        this.map.addLayer(this.customBaselayer);

        L.latlngGraticule({
          showLabel: true,
          dashArray: [0, 0],
          zoomInterval: [
            {start: 2, end: 3, interval: 30},
            {start: 4, end: 4, interval: 10},
            {start: 5, end: 7, interval: 10},
            {start: 8, end: 10, interval: 1}
          ]
        }).addTo(this.map);

        this.mousePosition = L.control.mousePosition().addTo(this.map);

      },

      exportData(){
        let that = this;
        this.axios({
          method: 'post',
          url: 'http://192.168.1.101:9002/eddy/export',
          data: {
            "dataType": that.dataType,
            "direction": that.eddyType,
            "maxStartTime": that.maxStartTime,
            "minStartTime": that.minStartTime
          },
          responseType: 'blob'
        }).then(res => {
          let data = res.data // 这里后端对文件流做了一层封装，将data指向res.data即可
          if (!data) {
            return
          }
          let url = window.URL.createObjectURL(new Blob([data]))
          let a = document.createElement('a')
          a.style.display = 'none'
          a.href = url
          a.setAttribute('download','excel.xls')
          document.body.appendChild(a)
          a.click() //执行下载
          window.URL.revokeObjectURL(a.href) //释放url
          document.body.removeChild(a) //释放标签
        }).catch((error) => {
          console.log(error)
        })
      },

      getTableData() {
        let that = this;
        this.axios.post('http://192.168.1.101:9002/eddy/query', {
          "dataType": that.dataType,
          "direction": that.eddyType,
          "maxStartTime": that.maxStartTime,
          "minStartTime": that.minStartTime
        }).then(function (response) {

          that.listData = response.data;
          that.tableData[0].countNum=that.listData.data.countNum;
          that.tableData[0].cyclonicNum=that.listData.data.cyclonicNum;
          that.tableData[0].duration=that.listData.data.duration;
          that.tableData[0].anticyclonicNum=that.listData.data.anticyclonicNum;
          that.rowspan();
        })
          .catch(function (error) {
            console.log(error);
          });
      },
      rowspan() {
        this.spanArr=[];
        this.listData.data.lstEddy.forEach((item, index) => {
          if (index === 0) {
            this.spanArr.push(1);
            this.position = 0;
          } else {
            if (this.listData.data.lstEddy[index].id === this.listData.data.lstEddy[index - 1].id) {
              this.spanArr[this.position] += 1;
              this.spanArr.push(0);
            } else {
              this.spanArr.push(1);
              this.position = index;
            }
          }
        })
      },
      objectSpanMethod({row, column, rowIndex, columnIndex}) {  //表格合并行
        if (columnIndex === 0) {
          const _row = this.spanArr[rowIndex];
          const _col = _row > 0 ? 1 : 0;
          return {
            rowspan: _row,
            colspan: _col
          }
        }
        if (columnIndex === 1) {
          const _row = this.spanArr[rowIndex];
          const _col = _row > 0 ? 1 : 0;
          return {
            rowspan: _row,
            colspan: _col
          }
        }
      },
    }
  };
</script>

<style scoped>
  #map {
    width: 100%;
    height: calc(100vh);
    z-index: 1;
  }

  /deep/ .el-input__inner {
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: 0;
  }

  /deep/ .el-picker-panel {
    background: rgba(0, 0, 0, 0.5);
  }

  /deep/ .leaflet-container a.leaflet-popup-close-button {
    display: none;
  }

  /deep/ .el-range-input {
    background: rgba(0, 0, 0, 0);
    color: white;
  }

  /deep/ .el-radio {
    color: white;
  }

  /deep/ .el-date-editor .el-range-separator {
    color: white;
  }

  .options {
    position: absolute;
    width: 70px;
    top: 110px;
    left: 60px;
    overflow: hidden;
    z-index: 11;
    transition: width 0.3s;
    -webkit-transition: width 0.3s; /* Safari */
  }

  .options:hover {
    width: 100px;
  }

  .animationbg {
    background-color: #fff;
  }

  .tableItem:hover {
    background-color: rgba(255, 255, 255, 0.5);
    color: white;
  }

</style>



